export default {
	data() {
		return {
			authKey:'',
			address:''
		};
	},
	onLoad(){
		this.authKey = uni.getStorageSync('authKey');
	},	
	onShow(){
		this.getAdress();
	},
	methods:{
		// 获取地址列表
		getAdress(){
			uni.request({
				url: this.http+'home/getAddressList',
				method: 'GET',
				header: {
					authKey: this.authKey
				},
				data: {},
				success: res => {
					console.log(res.data)
					if (res.data.code == 200) {
						this.address = res.data.data
					}
				},
			});
		},
		edit(e){
			uni.navigateTo({
				url: '/pages/mine/address/editAdress?id=' + e
			});
		},
		// 删除地址
		delet(e){
			uni.request({
				url: this.http+'home/delAddress/' + e,
				method: 'GET',
				header: {
					authKey: this.authKey
				},
				data: {},
				success: res => {
					console.log(res.data)
					if (res.data.code == 200) {
						uni.showToast({
							title:'删除地址成功',
							icon:'none',
							duration:1500
						})
						this.getAdress();
					}
				},
			});
		},
		addAddress(){
			uni.navigateTo({
				url: '/pages/mine/addAddress/addAddress'
			});
		}
	}
}
